@import url(https://fonts.googleapis.com/css?family=Short+Stack);


body {
	background: -webkit-radial-gradient(center ellipse,  #333333 0%,#333333 50%,#000000 100%);
	background: -o-radial-gradient(center ellipse,  #333333 0%,#333333 50%,#000000 100%);
	background: radial-gradient(ellipse at center,  #333333 0%,#333333 50%,#000000 100%); 
	background-size: 45px 45px; 
}
#root {
	margin: 2% auto;
	padding: 5px;
	width: 660px;
}

.App {
	border: 2px solid black;
	line-height: 0px;
	float: left;
}

#title {
	font-size: 17px;
	font-family: 'Short Stack', cursive;
	color: lightgoldenrodyellow;
}

#shadow {
	-webkit-box-shadow: 3px 3px 20px 3px black;
	        box-shadow: 3px 3px 20px 3px black;
}


